import React from 'react';
import s from './Dialogs.module.css';
import Message from "./Message/Message";
import DialogItem from "./DialogsItem/DialogItem";
import {Redirect} from "react-router-dom";
import AddMessageFormRedux from "./dialogsForm";


const Dialogs = (props) => {
    let state = props.messagesPage;
    let dialogsElements = state.dialogs.map(d => <DialogItem name={d.name} id={d.id} key={d.id}/>);//masiv
    let messagesElements = state.messages.map(m => <Message message={m.message} id={m.id} key={m.id}/>);//masiv
    let newMessageBody = state.newMessageBody;

    let AddNewMessage = (values) => {
        props.sendMessage(values.newMessageBody);
    };

    return (
        <div className={s.dialogs}>
            <div className={s.dialogsItems}>
                {dialogsElements}
            </div>
            <div className={s.messages}>
                {messagesElements}

                <AddMessageFormRedux onSubmit={AddNewMessage}/>
            </div>
        </div>
    )
};

export default Dialogs;